import React ,{useState}from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import authService from './services/authServices';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';


const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 730,
        marginTop: 80,
        fontFamily: `"Poppins", sans-serif`,
        backgroundColor: "#FAFAFA",
      },
    inputs: {
        display: 'flex',
        flexDirection: 'column',
        height: theme.spacing(40),
        width: theme.spacing(50),
        justifyContent: 'space-around',
        alignItems: 'center',
        marginLeft: theme.spacing(5)
        
      
    },
    cardContainer:{
        display: 'flex',
        justifyContent: 'space-evenly',
        alignItems: 'center',
        marginBottom: theme.spacing(20)
    },
    fill: {
        "& .MuiFilledInput-root": {
            background: "#FFFF"
          }

    }
  }));

function UpdateAccountComponent() {
    const [values, setValues] = useState({
        email:authService.getEmail(),
        name:authService.getName()
      });
    const [checkEmail, setCheckEmail] = useState(false);
    const [checkName, setCheckName] = useState(false);
    const [errorMessage, setErrorMessage] = useState(false);
    const classes = useStyles();



    const handleInputChange = (prop) => (e) => {
       
        switch(prop){
            case "name":
                if(e.target.value.length <3 ){
                    setCheckName(true);
                }
                else{
                    setCheckName(false);
                }
                break;
            case "email":
                let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                if ( re.test(e.target.value) ) {
                    setCheckEmail(false);
                
                }
                else{
                    setCheckEmail(true);

                }
                break;
        }

        setValues({ ...values, [prop]: e.target.value });
    }



    const handleSubmit = (prop) =>  (e) => {
        e.preventDefault();
        
      
        if( !checkEmail && !checkName){
            setErrorMessage(false);
            setValues({ ...values, [prop]: e.target.value });
            if(values.name !== authService.getName() || values.email !== authService.getEmail()){
                console.log(values)
            //updateUser(values); //call api to save user
            }
                

           

        }else{
            setErrorMessage(true);
        }

    }

    const updateUser = async (values) => {
        await authService.updateAccount(values).then(
            (response) => {
                console.log(response);
                
            })
        .catch( 
            (error) => {
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
                
            });
    
      
    
      }


  return (
    <div className={classes.cardContainer}>
       <Card className={classes.root}>
            <CardHeader classes={{ title: 'nTitle', subheader: 'nSubtitle' }}
                title="Update Account Information"
                subheader=" Choose a name and an email to update your account information"
            />
            <CardContent> 
                <form autoComplete="off" onSubmit={handleSubmit()} className={classes.inputs}>
            
                    <TextField className={classes.fill}
                        required
                        autoFocus
                        inputProps={{ maxLength: 30 }}
                        margin="dense"
                        id="name"
                        label="Name"
                        type="string"
                        variant="filled"
                        
                        onChange={handleInputChange('name')} 
                        value={values.name}
                        fullWidth
                        helperText={checkName ? 'Invalid name, must contain at least 6 characters' : ''}
                    />
                    <TextField className={classes.fill}
                        required
                        autoFocus
                        margin="dense"
                        id="name"
                        label="Email Address"
                        
                        onChange={handleInputChange('email')} 
                        value={values.email}
                        type="email"
                        variant="filled"
                        fullWidth
                        helperText={checkEmail ? 'Invalid Email format' : ''}
                    />
                    <Button color="primary" type="submit">
                        Update
                    </Button>
                </form>
            </CardContent> 

        </Card>

    </div>
  )
}

export default UpdateAccountComponent